<template>

  <img src="./assets/logo.png" alt="vue" srcset="">
  <HelloWorld />
  <h1>Mini projeto - Artigos</h1>
  <input type="text" placeholder="Buscar artigo pelo ID" class="search" v-model="search">

  <hr>

  <div v-for="(artigos, i) in artigosFiltrados" :key="i">
    <Artigo :title="artigos.title" :body="artigos.body" :id="artigos.id" />
  </div>
</template>

<script>
import HelloWorld from "./components/HelloWorld.vue";
import axios from "axios";
import Artigo from "../src/components/Artigo";

export default {
  name: "App",
  components: {
    HelloWorld,
    Artigo,
  },
  created() {
    axios
      .get("https://jsonplaceholder.typicode.com/posts")
      .then((res) => {
        this.artigos = res.data;
      })
      .catch((err) => {
        console.log(err);
      });
  },
  data() {
    return {
      search: "",
      artigos: [],
    };
  },
  computed: {
    artigosFiltrados(){

      if (this.search == undefined || this.search == " " || this.search == "") {
        return this.artigos;
      }else{
        return this.artigos.filter(artigos=> artigos.id == this.search);
      }
    }
  }
};
</script>

